import { Injectable } from '@angular/core';
import { Device } from '@ionic-native/device';
import { Http, Response, URLSearchParams, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { AddressInfo } from '../models/addressInfo';
import { Setting } from '../app/app.setting';

/*
  Generated class for the Login provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class AddressService {

  private addressListUrl = this.setting.apiTestHost + '/artradeApp/buyer/queryharvestaddr.action';
  private addAddressUrl = this.setting.apiTestHost + '/artradeApp/buyer/doaddorupdateaddr.action';
  private modifyAddressUrl = this.setting.apiTestHost + '/artradeApp/buyer/doaddorupdateaddr.action';
  private delAddressUrl = this.setting.apiTestHost + '/artradeApp/buyer/dodeleteaddr.action';
  private setDefaultAddressUrl = this.setting.apiTestHost + '/artradeApp/buyer/dodefauladdr.action';
  private getProvsUrl = this.setting.apiTestHost + '/artrade/common/region/getprovs.action';
  private getCitiesUrl = this.setting.apiTestHost + '/artrade/common/region/getcities.action';

  constructor(public http: Http, private setting: Setting, private device: Device) {
  }

  getProvs(): Observable<any[]> {
    let params = new URLSearchParams();
    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.getProvsUrl, options)
      .map((res) => {
        let response = res.json();
        if (response.result) {
          return response.data;
        }
        return null;
      })
      .catch(this.handleError);
  }

  getCities(prvncid: number): Observable<any[]> {
    let params = new URLSearchParams();
    params.set('prvncid', prvncid + '');
    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.getCitiesUrl, options)
      .map((res) => {
        let response = res.json();
        if (response.result) {
          return response.data;
        }
        return null;
      })
      .catch(this.handleError);
  }

  getAddressList(memberId: string): Observable<AddressInfo[]> {//根据专场id获取拍品列表
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId);
    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.addressListUrl, options)
      .map((res) => {
        let response = res.json();
        if (response.result) {
          return response.data;
        }
        return null;
      })
      .catch(this.handleError);
  }

  addAddress(memberId: string, address: AddressInfo): Observable<Boolean> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId);
    params.set('cneeName', address.cneeName);
    params.set('cneePhone', address.cneePhone + '');
    params.set('prvncId', address.prvncId + '');
    params.set('prvncName', address.prvncName);
    params.set('cityId', address.cityId + '');
    params.set('cityName', address.cityName);
    params.set('cneeAddr', address.cneeAddr);
    params.set('addrDflt', address.addrDflt);
    params.set('postCode', address.postCode);

    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.addAddressUrl, options)
      .map((res) => {
        let response = res.json();
        return response.result;
      })
      .catch(this.handleError);
  }

  modifyAddress(memberId: string, address: AddressInfo): Observable<Boolean> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId);
    params.set('addrId', address.addrId + '');
    params.set('cneeName', address.cneeName);
    params.set('cneePhone', address.cneePhone + '');
    params.set('prvncId', address.prvncId + '');
    params.set('prvncName', address.prvncName);
    params.set('cityId', address.cityId + '');
    params.set('cityName', address.cityName);
    params.set('cneeAddr', address.cneeAddr);
    params.set('addrDflt', address.addrDflt);
    params.set('postCode', address.postCode);

    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.modifyAddressUrl, options)
      .map((res) => {
        let response = res.json();
        return response.result;
      })
      .catch(this.handleError);
  }

  delAddress(memberId: string, addrId: number): Observable<Boolean> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId);
    params.set('id', addrId + '');

    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.delAddressUrl, options)
      .map((res) => {
        let response = res.json();
        return response.result;
      })
      .catch(this.handleError);
  }

  setDefaultAddress(memberId: string, addrId: number): Observable<Boolean> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId);
    params.set('addrId', addrId + '');

    let options = new RequestOptions({
      params: params,
      headers: new Headers()
    });
    return this.http.get(this.setDefaultAddressUrl, options)
      .map((res) => {
        let response = res.json();
        return response.result;
      })
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

}



