export class AddressInfo {
  addrId: number;//地址主键ID
  cneeName: string;//收货人姓名
  cneePhone: number;//收货人手机
  prvncId: number;//收货人地址省份ID
  prvncName: string;//收货人地址省份名称
  cityId: number;//收货人地址城市ID
  cityName: string;//收货人地址城市名称
  cneeAddr: string;//收货人地址
  addrDflt: string;//是否默认{"Y":"是","N":"否"}
  postCode: string;//邮政编码
}



